.nav {
  -ms-flex: 1 1 200px;
  flex: 1 1 200px;
  height: 100vh;
  background: #f9f7ff;
  /* Old Browsers */
  background: -webkit-linear-gradient(right, #f9f7ff, #ffffff);
  /*Safari 5.1-6*/
  background: -o-linear-gradient(right, #f9f7ff, #ffffff);
  /*Opera 11.1-12*/
  /*Fx 3.6-15*/
  background: -webkit-gradient(linear, right top, left top, from(#f9f7ff), to(#ffffff));
  background: -webkit-linear-gradient(right, #f9f7ff, #ffffff);
  background: -o-linear-gradient(right, #f9f7ff, #ffffff);
  background: linear-gradient(to left, #f9f7ff, #ffffff);
  /*Standard*/ }

.logo {
  font-size: 30px;
  margin: 10px 0 0 10px; }
  .logo span {
    font-size: 24px; }

.menu ul {
  margin: 40px 0 0 0;
  padding: 0; }
  .menu ul li {
    list-style-type: none;
    font-size: 24px;
    padding: 0 0 0 10px; }
    .menu ul li:hover {
      background-color: #e5e3ff;
      -webkit-box-shadow: 0 0 2px 0 #969696;
      box-shadow: 0 0 2px 0 #969696; }
    .menu ul li:active {
      background-color: #e5e3ff;
      -webkit-box-shadow: 0 0 2px 0 #969696;
      box-shadow: 0 0 2px 0 #969696; }
    .menu ul li a {
      text-decoration: none;
      color: inherit; }



